body {
  background-color: #000;
}

#landing-header {
  z-index: 1;
  position: relative;
  text-align: center;
  padding-top: 25vh;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 812px) {
  #landing-header {
    padding-top: 15vh;
  }
}

#landing-header h1 {
  color: #fff;
  font-size: 6em;
  width: 50%;
  margin: 0 auto;
  padding-bottom: 1em;
}
@media screen and (max-width: 950px) {
  #landing-header h1 {
    color: #fff;
    font-size: 4em;
    width: 100%;
    margin: 0 auto;
    padding-bottom: 1em;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 812px) {
  #landing-header h1 {
    font-size: 2.5em;
    padding-bottom: 1em;
    width: 100%;
  }
}

.buttons {
  width: 50%;
  padding-top: 1em;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 812px) {
  .buttons {
    position: fixed;
    bottom: 0;
    padding-bottom: 3em;
    width: 100%;
  }
}

.buttons div {
  margin: 0 2em;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 812px) {
  .buttons div {
    margin: 0 1em;
  }
}

.buttons div button {
  background-color: #1890ff;
  border: none;
  color: white;
  padding: 0.5em 1.2em;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5em;
  cursor: pointer;
  border-radius: 1.4em;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 812px) {
  .buttons div button {
    padding: 0.4em 0.9em;
    font-size: 1.4em;
    border-radius: 1.4em;
  }
}

.slideshow {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  list-style: none;
  margin: 0;
  padding: 0;
}

.slideshow li {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0;
  z-index: 0;
  animation: imageAnimation 35s linear infinite;
}

.slideshow li:nth-child(1) {
  background-image: url(https://images.unsplash.com/photo-1526374965328-7f61d4dc18c5?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80);
}
.slideshow li:nth-child(2) {
  background-image: url(https://images.unsplash.com/photo-1547700055-b61cacebece9?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80);
  animation-delay: 7s;
}
.slideshow li:nth-child(3) {
  background-image: url(https://images.unsplash.com/photo-1524985069026-dd778a71c7b4?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60);
  animation-delay: 14s;
}
.slideshow li:nth-child(4) {
  background-image: url(https://images.unsplash.com/photo-1536440136628-849c177e76a1?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60);
  animation-delay: 21s;
}
.slideshow li:nth-child(5) {
  background-image: url(https://images.unsplash.com/photo-1505686994434-e3cc5abf1330?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=600&q=60);
  animation-delay: 28s;
}

@keyframes imageAnimation {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
  }
  10% {
    opacity: 1;
    animation-timing-function: ease-out;
  }
  20% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
}
